// src/app/dashboard/page.js
'use client';

import { useState, useEffect } from 'react';
import { Box, Container, Typography, Paper, useTheme, useMediaQuery, Grid, CircularProgress, Snackbar, Alert } from '@mui/material';
import dynamic from 'next/dynamic';
import { getTopics, getSubtopics, getCompletedProblems, markAsCompleted, removeFromCompleted } from '@/services/dsaService';

// Dynamically import components for code splitting
const DSAChapterList = dynamic(() => import('@/components/DSA/ChapterList'), { 
  ssr: false,
  loading: () => <Box sx={{ p: 3, textAlign: 'center' }}>Loading chapters...</Box>
});
const DSAProgressTracker = dynamic(() => import('@/components/DSA/ProgressTracker'), { 
  ssr: false,
  loading: () => <Box sx={{ p: 2, textAlign: 'center' }}>Loading progress...</Box>
});
const ChapterDetail = dynamic(() => import('@/components/DSA/ChapterDetail'), {
  ssr: false,
  loading: () => <Box sx={{ p: 3, textAlign: 'center' }}>Loading problem details...</Box>
});

export default function Dashboard() {
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down('md'));
  const [selectedChapter, setSelectedChapter] = useState(null);
  const [chapters, setChapters] = useState([]);
  const [subtopics, setSubtopics] = useState([]);
  const [progress, setProgress] = useState({});
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState(null);
  const [snackbar, setSnackbar] = useState({ open: false, message: '', severity: 'success' });

  // Fetch all topics on component mount
  useEffect(() => {
    const fetchData = async () => {
      try {
        setIsLoading(true);
        const [topicsData, completedData] = await Promise.all([
          getTopics(),
          getCompletedProblems()
        ]);
        
        setChapters(topicsData.data || []);
        
        // Transform completed problems into progress object
        const completedProblems = {};
        (completedData.data || []).forEach(item => {
          if (!completedProblems[item.topic]) {
            completedProblems[item.topic] = {};
          }
          completedProblems[item.topic][item.subtopic] = true;
        });
        
        setProgress(completedProblems);
      } catch (err) {
        console.error('Error fetching data:', err);
        setError('Failed to load data. Please try again later.');
        setSnackbar({
          open: true,
          message: 'Failed to load data. Please try again later.',
          severity: 'error'
        });
      } finally {
        setIsLoading(false);
      }
    };

    fetchData();
  }, []);

  // Fetch subtopics when a chapter is selected
  useEffect(() => {
    if (!selectedChapter) return;

    const fetchSubtopics = async () => {
      try {
        setIsLoading(true);
        const response = await getSubtopics(selectedChapter.id);
        setSubtopics(response.data || []);
      } catch (err) {
        console.error('Error fetching subtopics:', err);
        setSnackbar({
          open: true,
          message: 'Failed to load subtopics. Please try again.',
          severity: 'error'
        });
      } finally {
        setIsLoading(false);
      }
    };

    fetchSubtopics();
  }, [selectedChapter]);

  const handleChapterSelect = (chapter) => {
    setSelectedChapter(chapter);
  };

  const handleToggleComplete = async (chapterId, problemId) => {
    try {
      const isCompleted = progress[chapterId]?.[problemId];
      
      if (isCompleted) {
        await removeFromCompleted(problemId);
      } else {
        await markAsCompleted(problemId);
      }
      
      // Update local state
      setProgress(prev => ({
        ...prev,
        [chapterId]: {
          ...prev[chapterId],
          [problemId]: !isCompleted
        }
      }));

      setSnackbar({
        open: true,
        message: isCompleted ? 'Removed from completed' : 'Marked as completed!',
        severity: 'success'
      });
    } catch (err) {
      console.error('Error updating completion status:', err);
      setSnackbar({
        open: true,
        message: 'Failed to update status. Please try again.',
        severity: 'error'
      });
    }
  };

  const handleCloseSnackbar = () => {
    setSnackbar(prev => ({ ...prev, open: false }));
  };

  return (
    <Container 
      maxWidth="xl" 
      sx={{ 
        py: 4,
        minHeight: '100vh',
        background: theme.palette.mode === 'light' 
          ? 'linear-gradient(135deg, #f5f7fa 0%, #e4e8ed 100%)'
          : theme.palette.background.default,
      }}
    >
      <Box sx={{ 
        maxWidth: 1600, 
        mx: 'auto',
        p: { xs: 2, md: 3 },
        borderRadius: 2,
        background: theme.palette.background.paper,
        boxShadow: theme.shadows[1]
      }}>
        <Box sx={{ mb: 4, textAlign: 'center' }}>
          <Typography 
            variant="h4" 
            component="h1" 
            sx={{ 
              fontWeight: 700,
              background: 'linear-gradient(45deg, #1976d2 0%, #2196f3 100%)',
              WebkitBackgroundClip: 'text',
              WebkitTextFillColor: 'transparent',
              mb: 1
            }}
          >
            DSA Learning Path
          </Typography>
          <Typography 
            variant="subtitle1" 
            color="text.secondary"
            sx={{ maxWidth: 600, mx: 'auto' }}
          >
            Track your progress and master Data Structures and Algorithms
          </Typography>
        </Box>

        <Grid container spacing={3}>
          {/* Left Sidebar - Chapter List */}
          <Grid item xs={12} md={4} lg={3}>
            {isLoading && !chapters.length ? (
              <Box sx={{ display: 'flex', justifyContent: 'center', p: 4 }}>
                <CircularProgress />
              </Box>
            ) : error ? (
              <Paper sx={{ p: 3, textAlign: 'center', color: 'error.main' }}>
                {error}
              </Paper>
            ) : (
              <DSAChapterList 
                chapters={chapters}
                onSelectChapter={handleChapterSelect} 
                selectedChapter={selectedChapter}
                progress={progress}
              />
            )}
          </Grid>

          {/* Main Content - Problem Details */}
          <Grid item xs={12} md={8} lg={6}>
            {isLoading && selectedChapter ? (
              <Box sx={{ display: 'flex', justifyContent: 'center', p: 4 }}>
                <CircularProgress />
              </Box>
            ) : selectedChapter ? (
              <ChapterDetail 
                chapter={selectedChapter}
                subtopics={subtopics}
                progress={progress[selectedChapter.id] || {}}
                onToggleComplete={handleToggleComplete}
              />
            ) : (
              <Paper 
                elevation={0}
                sx={{
                  p: 4,
                  height: '100%',
                  display: 'flex',
                  flexDirection: 'column',
                  alignItems: 'center',
                  justifyContent: 'center',
                  textAlign: 'center',
                  minHeight: 400,
                  borderRadius: 2,
                  backgroundColor: theme.palette.background.default,
                  border: `1px solid ${theme.palette.divider}`
                }}
              >
                <Box
                  sx={{
                    width: 80,
                    height: 80,
                    borderRadius: '50%',
                    bgcolor: theme.palette.action.hover,
                    display: 'flex',
                    alignItems: 'center',
                    justifyContent: 'center',
                    mb: 2
                  }}
                >
                  <Box
                    component="span"
                    sx={{
                      fontSize: 40,
                      color: theme.palette.text.secondary,
                      lineHeight: 1
                    }}
                  >
                    ðŸ“š
                  </Box>
                </Box>
                <Typography 
                  variant="h6" 
                  color="text.secondary"
                  sx={{ mb: 1, fontWeight: 500 }}
                >
                  Select a chapter to begin
                </Typography>
                <Typography 
                  variant="body2" 
                  color="text.disabled"
                  sx={{ maxWidth: 300 }}
                >
                  Choose a topic from the left panel to view problems and track your progress
                </Typography>
              </Paper>
            )}
          </Grid>

          {/* Right Sidebar - Progress Tracker */}
          {!isMobile && (
            <Grid item xs={12} lg={3}>
              <DSAProgressTracker 
                progress={progress} 
                totalChapters={chapters.length}
                totalSubtopics={chapters.reduce((acc, chapter) => acc + (chapter.subtopics?.length || 0), 0)}
              />
            </Grid>
          )}
          
          {/* Snackbar for notifications */}
          <Snackbar 
            open={snackbar.open} 
            autoHideDuration={6000} 
            onClose={handleCloseSnackbar}
            anchorOrigin={{ vertical: 'bottom', horizontal: 'right' }}
          >
            <Alert 
              onClose={handleCloseSnackbar} 
              severity={snackbar.severity} 
              sx={{ width: '100%' }}
              variant="filled"
            >
              {snackbar.message}
            </Alert>
          </Snackbar>
        </Grid>
      </Box>
    </Container>
  );
}