// src/services/dsaService.js
import { api } from '@/apiHandler/page';

// Topics API
export const getTopics = async () => {
  try {
    return await api.get('/topics');
  } catch (error) {
    console.error('Error fetching topics:', error);
    throw error;
  }
};

export const getTopic = async (id) => {
  try {
    return await api.get(`/topics/${id}`);
  } catch (error) {
    console.error(`Error fetching topic ${id}:`, error);
    throw error;
  }
};

// Subtopics API
export const getSubtopics = async (topicId) => {
  try {
    return await api.get(`/subtopics/topic/${topicId}`);
  } catch (error) {
    console.error(`Error fetching subtopics for topic ${topicId}:`, error);
    throw error;
  }
};

export const getSubtopic = async (id) => {
  try {
    return await api.get(`/subtopics/${id}`);
  } catch (error) {
    console.error(`Error fetching subtopic ${id}:`, error);
    throw error;
  }
};

// Progress API
export const getCompletedProblems = async () => {
  try {
    return await api.get('/subtopics/completed');
  } catch (error) {
    console.error('Error fetching completed problems:', error);
    throw error;
  }
};

export const markAsCompleted = async (subtopicId) => {
  try {
    return await api.post(`/subtopics/${subtopicId}/complete`);
  } catch (error) {
    console.error(`Error marking subtopic ${subtopicId} as completed:`, error);
    throw error;
  }
};

export const removeFromCompleted = async (subtopicId) => {
  try {
    return await api.delete(`/subtopics/${subtopicId}/complete`);
  } catch (error) {
    console.error(`Error removing subtopic ${subtopicId} from completed:`, error);
    throw error;
  }
};

export default {
  getTopics,
  getTopic,
  getSubtopics,
  getSubtopic,
  getCompletedProblems,
  markAsCompleted,
  removeFromCompleted,
};